@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Jost&display=swap');


:root{
  --blue:#6480C7;
  --red:#b30000;
  --white:#f3f3f3;
  --black:#212121;
}
::-webkit-scrollbar {
  width: 19px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 2px;
  background: #3A3939;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--blue);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--red);
}

